body.light-theme {
    background-color: #fff;
    color: #545454;
}

body.dark-theme {
    background-color: #333333;
    color: #fff;

    & .tui-page-btn {
            background-color: #333333;
            color: lightgrey;
        }
    & .tui-next-is-ellip {
            background-color: #333333;
            color: lightgrey;
        }
    & .gallery__name {
            color: #cccccc
        }
}

.toolbar {
    display: flex;
    justify-content: flex-end;
    padding: 16px 24px;
    position: absolute;
    right: 0;
    bottom: 0;
}

.theme-switch {
    display: flex;
    align-items: center;
}

.theme-switch__control {
    display: flex;
    align-items: center;
    flex-shrink: 0;
    position: relative;
    width: 33px;
    height: 18px;
    border-radius: 50em;
    padding: 3px 0;
}

.theme-switch__toggle {
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    padding: 0;
    opacity: 0;
    height: 0;
    width: 0;
    pointer-events: none;
}

.theme-switch__track {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    color: transparent;
    user-select: none;
    background-color: rgb(132, 114, 114);
    border-radius: inherit;
    z-index: 1;
    cursor: pointer;
}

.theme-switch__marker {
    position: relative;
    background-color: #fff;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    z-index: 2;
    pointer-events: none;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25);
    transform: translateX(3px);
    transition: transform 250ms;
}

.theme-switch__toggle:checked+.theme-switch__track {
    background-color: #FF6B08;
}

.theme-switch__toggle:checked~.theme-switch__marker {
    transform: translateX(17px);
}

.theme-switch__toggle:focus+.theme-switch__track,
.theme-switch__toggle:active+.theme-switch__track {
    box-shadow: 0 0 0 3px #FF6B08;
}

.dark-theme {
    color: lightgrey;
    background-color: #222;
}
